import React from "react";
import styled from "styled-components";

import FaqIconFile from "../utils/images/public/faqIcon.gif";
import {
  FaDiscord,
  FaTelegram,
  FaGithub,
  FaSkype,
  FaGoogle
} from "react-icons/fa";

import { SocialIcon} from "react-social-icons";

const FaqItem = (props) => {
  let network;
  if(props.title =='Gmail')
    network = 'google';
  if(props.title =='Skype')
    network = 'https://join.skype.com/invite/xWnb2Sg2OE0g';
  if(props.title =='Discord')
    network = 'https://discord.gg/mwCVFeWB';
  if(props.title =='Telegram')
    network = 'https://t.me/Danger_GreenPig';
  if(props.title =='Github')
    network = 'https://github.com/JackieMaster';
  return (
    <Wrapper onClick={props.onClick}>
        <SocialIcon url={network}/>
      <FaqTitle>{props.title}</FaqTitle>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  padding: 9px 5px;
  gap: 12px;
  cursor: pointer;
  border-radius: 10px;
  user-select: none;

  &: hover {
    background-color: rgba(188, 188, 188, 0.2);
  }
`;
const FaqIcon = styled.div`
  min-width: 40px;
  height: 40px;
  border-radius: 50%;
`;
const FaqContentWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;
const FaqContentHeader = styled.div`
  display: flex;
  align-items: center;
  gap: 5px;
`;
const FaqTitle = styled.div`
  color: #212b35;
  font-size: 14px;
  font-weight: 600;
`;
const FaqTime = styled.div`
  font-size: 12px;
  font-weight: 400;
`;
const FaqContent = styled.div`
  color: #212b35;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
`;
export default FaqItem;
